import React, { Fragment, useState, useEffect } from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPhone,
  faHome,
  faClock,
  faCalendarAlt,
  faUsers,
  faBook,
  faMapMarkerAlt,
  faPaperPlane,
} from "@fortawesome/free-solid-svg-icons";
import { faFacebookSquare } from "@fortawesome/free-brands-svg-icons";
import { Dialog, Transition } from "@headlessui/react";

import Button from "components/Button";
import Card from "components/Card";
import { zipCodesServed } from "ZIPcodes";

import "./App.css";

export default function App() {
  const handleSubmitForm = (e) => {
    e.preventDefault();

    // TODO: check Netlify form submit process
    // TODO: replace form content with success or fail message
  };

  const phrases = [
    "first home",
    "next home",
    "dream home",
    "new office",
    "mountain cabin",
    "lakehouse",
  ];
  const [index, setIndex] = useState(0);
  useEffect(() => {
    const interval = setInterval(() => {
      // update current word
      setIndex((index) => index + 1);
    }, 3500); // every 3 seconds

    return () => clearInterval(interval);
  }, []);

  const [serviceable, setServiceable] = useState(false);
  const [zip, setZip] = useState("");

  const [isReviewsDialogOpen, setIsReviewsDialogOpen] = useState(false);
  const [isModernInspectionOpen, setIsModernInspectionOpen] = useState(false);
  const [isInsuredDialogOpen, setIsInsuredDialogOpen] = useState(false);
  const [isBookResidentialDialogOpen, setIsBookResidentialDialogOpen] =
    useState(false);
  const [isBookCommercialDialogOpen, setIsBookCommercialDialogOpen] =
    useState(false);
  const [isScheduleDialogOpen, setIsScheduleDialogOpen] = useState(false);

  return (
    <div className="bg-ciWhite" role="main">
      {/* HEADER */}
      <div className="relative flex flex-col justify-center items-center h-big bg-houseSm md:bg-houseLg bg-cover md:bg-center">
        <div className="bg-logo bg-center bg-contain bg-no-repeat w-32 h-32 relative lg:top-3"></div>

        <div className="w-screen my-4 px-1 text-shadow leading-snug tracking-wider text-white text-center text-4xl md:text-5xl lg:text-7xl max-w-7xl font-black">
          <p>Buy your</p>{" "}
          <p className="text-ciPop">{phrases[index % phrases.length]}</p>{" "}
          <p>with confidence</p>
        </div>

        <div className="absolute -bottom-6">
          <Button
            onClick={() => setIsScheduleDialogOpen(true)}
            icon="faCalendarAlt"
            text="Schedule Your Inspection"
          ></Button>
        </div>
      </div>

      {/* WHY US */}
      <div className="my-20">
        <p
          className="leading-snug tracking-wider text-center text-4xl font-extrabold"
          role="heading"
          aria-level="1"
        >
          Why Core Inspection?
        </p>

        <div className="flex flex-col lg:flex-row items-center justify-center">
          <Card
            backgroundImage="bg-reviews"
            text="Reviews"
            alt="Reviews and testimonials"
            onClick={() => setIsReviewsDialogOpen(true)}
          ></Card>
          <Transition appear show={isReviewsDialogOpen} as={Fragment}>
            <Dialog
              as="div"
              className="fixed inset-0 z-10 overflow-y-auto"
              onClose={() => setIsReviewsDialogOpen(false)}
            >
              <div className="min-h-screen px-4 text-center">
                <Transition.Child
                  as={Fragment}
                  enter="ease-out duration-300"
                  enterFrom="opacity-0"
                  enterTo="opacity-100"
                  leave="ease-in duration-200"
                  leaveFrom="opacity-100"
                  leaveTo="opacity-0"
                >
                  <Dialog.Overlay className="fixed inset-0" />
                </Transition.Child>

                {/* This element is to trick the browser into centering the modal contents. */}
                <span
                  className="inline-block h-screen align-middle"
                  aria-hidden="true"
                >
                  &#8203;
                </span>
                <Transition.Child
                  as={Fragment}
                  enter="ease-out duration-300"
                  enterFrom="opacity-0 scale-95"
                  enterTo="opacity-100 scale-100"
                  leave="ease-in duration-200"
                  leaveFrom="opacity-100 scale-100"
                  leaveTo="opacity-0 scale-95"
                >
                  <div className="inline-block w-full max-w-md p-6 my-8 overflow-hidden text-left align-middle transition-all transform bg-white shadow-xl rounded">
                    <Dialog.Title
                      as="h3"
                      className="text-lg font-medium leading-6 text-gray-900"
                    >
                      Customer Reviews
                    </Dialog.Title>
                    <div className="mt-2">
                      <p className="text-sm text-gray-500">
                        We're honored that so many have trusted us. Here's what
                        people think of our service.
                      </p>
                    </div>

                    <div className="mt-4">
                      <button
                        type="button"
                        className="inline-flex justify-center px-4 py-2 text-sm font-medium bg-ciGreen-light text-white border border-transparent rounded hover:bg-ciGreen-medium focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-green-500"
                        onClick={() => setIsReviewsDialogOpen(false)}
                      >
                        Close
                      </button>
                    </div>
                  </div>
                </Transition.Child>
              </div>
            </Dialog>
          </Transition>
          <Card
            backgroundImage="bg-sampleReport"
            text="Comprehensive Report"
            alt="View a sample report"
            onClick={() =>
              window.open(
                "https://www.homegauge.com/report/14539356/FullReportForUpload.html",
                "_blank"
              )
            }
          ></Card>
          <Card
            backgroundImage="bg-drone"
            text="Modern Inspection"
            alt="Modern inspection"
            onClick={() => setIsModernInspectionOpen(true)}
          ></Card>
          <Card
            backgroundImage="bg-certified"
            text="Insured & InterNACHI Certified"
            alt="Insured and InterNACHI certified"
            onClick={() => setIsInsuredDialogOpen(true)}
          ></Card>
        </div>
      </div>

      {/* SERVICES */}
      <div className="bg-ciGreen bg-opacity-70 pt-8 pb-14 flex flex-col items-center">
        <p
          className="leading-snug tracking-wider text-4xl text-white font-extrabold"
          role="heading"
          aria-level="1"
        >
          Services
        </p>

        <p className="text-white">Don't see a time that works for you?</p>
        <div className="flex items-center space-x-2 text-white pb-3">
          <p>Give us a call at</p>
          <a href="tel:+17062065512">
            <FontAwesomeIcon icon={faPhone} /> (706) 206-5512
          </a>
        </div>

        <div className="flex flex-col lg:flex-row items-center justify-center w-full">
          <Card
            backgroundImage="bg-residential"
            text="Book Residential"
            alt="Residential booking"
            onClick={() => setIsBookResidentialDialogOpen(true)}
          ></Card>
          <Card
            backgroundImage="bg-commercial"
            text="Book Commercial"
            alt="Commercial booking"
            onClick={() => setIsBookCommercialDialogOpen(true)}
          ></Card>
        </div>
      </div>

      {/* PROCESS */}
      <div className="my-24 px-2">
        <p
          className="mb-5 leading-snug tracking-wider text-center text-4xl font-extrabold"
          role="heading"
          aria-level="1"
        >
          The Process
        </p>

        <div className="flex flex-col items-center space-y-10">
          <div className="flex flex-col items-center space-y-3 text-center">
            <div className="w-20 h-20 border-black border-4 font-bold text-2xl rounded-full flex justify-center items-center">
              <FontAwesomeIcon icon={faMapMarkerAlt} size="lg" />
            </div>
            <p>
              <label className="text-xl flex flex-col items-center">
                Check if we serve your ZIP code
                <input
                  name="zip"
                  type="text"
                  maxLength="5"
                  autoComplete="off"
                  onChange={(e) => {
                    setServiceable(zipCodesServed.includes(e.target.value));
                    setZip(e.target.value);
                  }}
                  className="w-60 p-2 mt-2 text-center rounded-sm shadow-md border-b-4 border-ciGreen border-opacity-80"
                  style={{
                    backgroundColor:
                      serviceable && zip.length > 0
                        ? "rgba(101, 155, 94,0.25)"
                        : !serviceable && zip.length > 4
                        ? "rgba(239, 68, 68, 0.25)"
                        : "#FFFFFF",
                  }}
                />
              </label>
            </p>
            {serviceable && (
              <p className="max-w-xs text-ciGreen-light text-lg">
                You're in our service area!
              </p>
            )}
            {!serviceable && zip.length > 4 && (
              <p className="max-w-xs text-red-500 text-lg">
                Sorry, it looks like you live outside of our service area.
              </p>
            )}
          </div>

          <div className="flex flex-col items-center space-y-3 text-center">
            <div className="w-20 h-20 border-black border-4 font-bold text-2xl mb-1 rounded-full flex justify-center items-center">
              <FontAwesomeIcon icon={faCalendarAlt} size="lg" />
              {/* TODO: add calendar modal here */}
            </div>
            <Button
              onClick={() => setIsScheduleDialogOpen(true)}
              text="Schedule Your Inspection"
            ></Button>
          </div>

          <div className="flex flex-col items-center space-y-3 text-center">
            <div className="w-20 h-20 border-black border-4 font-bold text-2xl rounded-full flex justify-center items-center">
              <FontAwesomeIcon icon={faUsers} size="lg" />
            </div>
            <p className="text-xl">Our team goes to work</p>
          </div>

          <div className="flex flex-col items-center space-y-3 px text-center">
            <a
              href="https://www.homegauge.com/report/14539356/FullReportForUpload.html"
              target="_blank"
              rel="noreferrer"
              aria-label="Homegauge"
            >
              <div
                className="transform hover:scale-110 motion-reduce:transform-none cursor-pointer w-20 h-20 border-black border-4 font-bold text-2xl rounded-full flex justify-center items-center"
                onClick={() => console.log("binder should open")}
              >
                <FontAwesomeIcon icon={faBook} size="lg" />
              </div>
            </a>
            <p className="text-xl">Your inspection binder is yours forever</p>
          </div>
        </div>
      </div>

      {/* CONTACT FORM */}
      <div className="mt-14 px-2 text-center max-w-6xl m-auto">
        <p
          className="leading-snug tracking-wider text-center text-4xl font-extrabold"
          role="heading"
          aria-level="1"
        >
          Get in Touch
        </p>

        <p>We'll reply within a business day.</p>

        <form
          name="contact"
          method="POST"
          data-netlify="true"
          onSubmit={handleSubmitForm}
          className="bg-ciGreen bg-opacity-70 flex flex-col items-center space-y-4 my-3 py-5 rounded-md"
        >
          <p>
            <label className="tracking-wider font-bold flex flex-col text-white">
              Your Name{" "}
              <input
                className="w-60 p-2 text-center text-black rounded-sm"
                type="text"
                name="name"
              />
            </label>
          </p>
          <p>
            <label className="tracking-wider font-bold flex flex-col text-white">
              Your Email{" "}
              <input
                className="w-60 p-2 text-center text-black rounded-sm"
                type="email"
                name="email"
              />
            </label>
          </p>
          <p>
            <label className="tracking-wider font-bold flex flex-col items-center text-white">
              Message{" "}
              <textarea
                className="w-60 md:w-96 h-16 p-1 text-black rounded-sm"
                name="message"
              ></textarea>
            </label>
          </p>
          <p>
            <button
              className="font-bold bg-white px-10 py-2 rounded-sm hover:bg-ciWhite flex items-center"
              type="submit"
            >
              <p className="mr-4">Send</p>
              <FontAwesomeIcon icon={faPaperPlane} />
            </button>
          </p>
        </form>
      </div>

      {/* Footer */}
      <div className="w-full bg-ciGreen-dark text-white py-4 flex flex-col space-y-5">
        <div className="flex flex-col space-y-3 items-center">
          <div className="bg-vetOwned h-28 w-28 mt-5 bg-contain bg-center bg-no-repeat"></div>
          <a
            href="https://www.facebook.com/coreinspect/"
            target="_blank"
            rel="noreferrer"
            aria-label="Facebook"
          >
            <FontAwesomeIcon icon={faFacebookSquare} size="3x" />
          </a>
          <div className="flex items-center space-x-2">
            <a href="tel:+17062065512">
              <FontAwesomeIcon icon={faPhone} /> (706) 206-5512
            </a>
          </div>
          <div className="flex items-center space-x-2">
            <FontAwesomeIcon icon={faHome} />
            <p>P.O. Box 492 Crawford, GA 30630</p>
          </div>
          <div className="flex flex-col items-center">
            <div className="flex space-x-2">
              <FontAwesomeIcon icon={faClock} />
              <p>Hours</p>
            </div>
            <p>Monday - Friday ... 8am - 6pm</p>
            <p>Saturday ... 8am - 2pm</p>
          </div>
          <div className="text-center text-xs py-4">
            <p>Â© Copyright 2021 by Core Inspection, LLC</p>
          </div>
        </div>
      </div>
    </div>
  );
}
